* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

form {
  background-color: #f5f5f5;
  padding: 1rem;
  margin: 2rem auto;
  width: 32rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);

  margin-bottom: 1rem;
}
.login_form{
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  color: rgb(0, 0, 0);
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 0.5rem;
  padding-right: 20px;
}
.login_form label {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  color: rgb(0, 0, 0);
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 0.5rem;
  padding-right: 20px;
}

.login_form input {
  width: 100%;
  padding: 12px 15px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.btn {
  font-weight: bold;
  font-size: 1.5rem;
  background-color: #04aa6d;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
}

.btn:hover {
  opacity: 0.8;
}

.data_store{

  font-size: 10px;
 
}